import { ref, reactive } from 'vue';
import verificationCodeDialog from "@/components/verificationCodeDialog.vue";
export default {
  name: "account",
  components: {
    verificationCodeDialog
  },

  setup() {
    const verificationVisible = ref(false);
    const accountForms = ref(null);
    const accountForm = reactive({
      password: '',
      confirm_password: '',
      current_password: ''
    });
    const accountRules = reactive({
      email_code: [{
        required: true,
        message: 'Please fill out your code.',
        trigger: 'blur'
      }],
      email: [{
        type: 'email',
        required: true,
        message: 'Please fill out your email address.',
        trigger: 'blur'
      }],
      password: [{
        required: true,
        message: 'Please enter your password',
        trigger: 'blur'
      }],
      confirm_password: [{
        required: true,
        message: 'Please enter your password again',
        trigger: 'blur'
      }]
    });

    function confirmDelete() {
      verificationVisible.value = true;
    }

    return {
      accountForms,
      accountForm,
      accountRules,
      verificationVisible,
      confirmDelete
    };
  }

};